@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:border-box}body{margin:0;padding:0;line-height:1;font-family:"Roboto", sans-serif;color:#202020;background-color:#fafafa}.header{border-bottom:solid 1px #ca2100;background-color:#db43cf;box-shadow:0 1px 2px rgba(0,0,0,0.15);height:44px;width:100%}.header img{width:44px;height:44px;border-radius:50% 0px 50% 50%}.header nav{height:100%;max-width:992px;display:flex;align-items:center;margin:auto}.content{display:flex;max-width:992px;margin:auto}.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:266px;height:100vh;padding-top:74px;border-right:1px solid #f1f1f1;background-color:#fafafa}.sidebar div{margin-bottom:10px;cursor:pointer;padding:15px}.sidebar div.active{background-color:#fff}.sidebar div .icon{margin-right:10px}.tasks{background-color:white;height:100vh;width:626px;padding:74px 20px 0}.tasks .add-task-btn{cursor:pointer}.tasks .add-task-btn .plus{color:#dd4b19;font-size:14px;margin-left:15px}.tasks .add-task-btn .add-task-text{color:#545454;font-size:14px;margin-left:15px}.tasks .task-item-container{margin-top:40px;padding:0 20px}.tasks .task-item-container .task-item{display:flex;justify-content:space-between}.tasks .task-item-container .task-item:not(:last-child){border-bottom:1px solid #db4c31}.tasks .add-task-dialog{margin-top:10px}.tasks .add-task-dialog input{width:100%;border:2px solid #ddd;background-color:white;border-radius:3px;height:35px;padding-left:10px;padding-right:10px}.tasks .add-task-dialog .add-task-actions-container{margin-top:10px;display:flex;justify-content:space-between}.tasks .add-task-dialog .add-task-actions-container .add-btn{background-color:#db4c13;color:white;border:1px solid transparent;margin-left:5px;font-weight:bold;font-size:13px;padding:6px 12px 7px 12px;border-radius:3px !important}.tasks .add-task-dialog .add-task-actions-container .add-btn:disabled{background-color:gray}.tasks .add-task-dialog .add-task-actions-container .add-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.tasks .add-task-dialog .add-task-actions-container .cancel-btn{all:unset;color:#555;cursor:pointer;font-size:14px;margin:0px 5px;border-radius:3px}

